import { Component, NgZone, OnInit } from '@angular/core';
import { ApiservicesService } from '../../provider/apiservices.service';
import { Router } from "@angular/router";
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { MenuController } from '@ionic/angular';
import { DataTransferService } from '../../services/data-transfer.service';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-hr-policy-category',
  templateUrl: './hr-policy-category.page.html',
  styleUrls: ['./hr-policy-category.page.scss'],
})
export class HrPolicyCategoryPage implements OnInit {
  errorMessage: any;
  no_data_message: any;
  hrPolicyListCategoryData: any;
  hrPolicyListCategory: any;
  pageHeader: any;

  constructor(private apiservices:ApiservicesService,private router:Router,private zone:NgZone,public menuCtrl:MenuController,
    private sendData:DataTransferService,private storageService:LocalStorageService) { }

  ngOnInit() {
    this.hrPolicyFunc();
  }

  hrPolicyFunc(){
    this.zone.run(()=>{
      console.log("HrPolicy List api call function");
      let apikey={
      }
      this.apiservices.apiCallWithLoginToken(URLS.hrPolicyCategoryApi, apikey).subscribe((result)=>{
        console.log("HrPolicy List api result==", result);
        this.pageHeader=result.data.settings.page_header;
        if(result.status==1 || result.status=='1'){
          this.hrPolicyListCategoryData=result.data;
            this.hrPolicyListCategory=this.hrPolicyListCategoryData.hrpolicy;
            console.log("HrPolicy List Infinite Scroll if1==", this.hrPolicyListCategory);
        }else{
          this.errorMessage = result.message;
          this.no_data_message=result.data.settings.no_data_msg;
          // this.showErrorImage=result.data.settings.no_data_image;
        }
      }, err =>{
        this.apiservices.showToastMessage(JSON.stringify(err),'top',3000,'redBg');
      })
    });
  }

  hrpolicyListFun(item){
    this.sendData.alldata=item;
    this.router.navigate(['/hr-policy-list'])
  }

}
